body{
    background-color: #f3f3f3;
    animation: fadeInAnimation ease 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}
@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
     }
}
.navbar1{
    background: transparent;
}
.dot {
    margin-right: 15px;
    height: 25px;
    width: 25px;
    background-color: #eea302;
    border-radius: 50%;
    display: inline-block;
  }
.name{
    font-size: 30px;
    font-family: Lucida Console;
    font-weight: bold;
    word-spacing: -10px;
}
.sub-name{
    font-size: 15px;
}
.nav_list{
    margin-right: 50px;
}
.buttonlist{
    background: transparent;
    border: none;
}
.buttonlist2{
    border-style: solid;
    border-width: 0px 1px 0px 1px; 
}
.buttonlist:hover, .buttonlist2:hover{
    color: brown;
}
.three-dot{
    background: transparent;
    border: none;
    font-size: 30px;
}
.image{
    max-width: 80%;
    max-height: 100%;
    border-radius: 50%;
}
.h1{
    font-size: 100px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: bold;
       
}
.h2{
    font-size: 25px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.h3{
    font-size: 20px;
    max-width: 30rem;
    /* font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;     */
}
.div2{
    width: 40rem;
}
.resume{
    background-color: #eea302;
    width: 150px;
    height: 150px;
    border: none;
    border-radius: 50%;
    
}
.project{
    background-color: #FF3B25;
    width: 150px;
    height: 150px;
    border: none;
    border-radius: 50%;
}
.contact{
    background-color: #80dad8;
    width: 150px;
    height: 150px;
    border: none;
    border-radius: 50%;
}
.resume:hover{
    background: transparent;
    border: 1px solid black;
}
.project:hover{
    background: transparent;
    border: 1px solid black;
}
.contact:hover{
    background: transparent;
    border: 1px solid black;
}
hr{
    height: 1px;
    background-color: rgb(0, 0, 0);
    border: none;
}
.footer{
    margin-top: 100px;
}

@media only screen and (max-width: 600px) {
    .footer {
        margin-top: 40px;
    }
    hr{
        height: 1px;
    }
    .dot {
        margin-left: 5px;
        margin-right: 5px;
        height: 20px;
        width: 20px;
        background-color: #eea302;
        border-radius: 50%;
        display: inline-block;
      }
    .name{
        font-size: 25px;
        font-family: Lucida Console;
        font-weight: bold;
        word-spacing: -10px;
    }
    .sub-name{
        font-size: 13px;
    }
    .nav_list{
        margin-right: 10px;
        display: none;
    }
    .buttonlist, .buttonlist2{
        display: none;
    }
  }

.head{
    font-size: 18px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: bold;
}
.sub_head{
    font-size: 16px;

}
p{
    margin: 0px;
}